import React from 'react';
import {connect} from 'react-redux'
import { Link } from 'react-router-dom';

import './Implant.css';
// import {updateCurrentPage} from "../../Store/actions/global.action";
import { surveyGetLocalAction } from '../../Store/actions/global.action'

class Implant extends React.Component {
  constructor(props){
    super(props);
  }
  componentWillMount(){
    const store = this.props
    store.dispatch(surveyGetLocalAction())
  }
  render() {
    {document.title = ""}
    return (
        <div>
          <p className="explain">
            <br/><br/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            
          </p>
          <div className="unit">
            <p><strong>:</strong></p>
            <p> </p>
            <p> </p>
          </div>
          <ul style={{width:'100%',marginTop:'2.5rem',textAlign:'center'}}>
            <li className="white_btn" style={{width:'40%',height:'6.6%',display:'inline-block'}}>
              <Link to='/un_implants' className="blueColor"></Link>
            </li>
            <li className="blue_btn" style={{width:'40%',height:'6.6%',display:'inline-block',marginLeft:'0.5rem'}}>
              <Link to='/querys/0' className="whiteColor"></Link>
            </li>
          </ul>
        </div>
    )
  }
}

export default connect(state => state)(Implant);



