import React from 'react';
import { Link } from 'react-router-dom';

import { updateCurrentPage } from '../../Store/actions/global.action';

class Home extends React.Component {
  constructor(props){
    super(props);
  }
  render() {
    {document.title = ""}
    const app_text = {
      width:'5.86rem',
      height:'1rem',
      textAlign: 'center',
      fontSize: '0.36rem',
      color: '#666666',
      margin: '1.04rem auto 0',
    };
    return (
        <div>
          <p style={app_text}><br/></p>
          <ul>
            <li className="blue_btn" style={{marginTop:'0.64rem'}}>
              <Link to='/implant' className="whiteColor"></Link>
            </li>
            <li className="white_btn" style={{marginTop:'0.52rem'}}>
              <Link to='/un_implants' className="blueColor"></Link>
            </li>
          </ul>
        </div>
    );
  }
}

export default Home;



